/* ==========================================================================
   Footer style - begin
   ========================================================================== */
/* ==========================================================================
   #Content bottom styles - start
   ========================================================================== */
.eltdf-content-bottom {

    .eltdf-content-bottom-inner{
        box-sizing: border-box;

        .widget {
            margin-bottom: 0;
        }
        
        &.eltdf-grid{
            padding: 20px 0;
        }
    }
}

.eltdf-page-footer {
    @include eltdfRelativeHolderLayout();
    z-index: 100;
    box-sizing: border-box;

    .eltdf-footer-top-holder {
        @include eltdfRelativeHolderLayout();
        background-color: #212121;
        box-sizing: border-box;

        .eltdf-subscription-form {
            position: relative;
            width: 80%;
            .wpcf7-form-control.wpcf7-text {
                border: 1px solid #383838;
                background-color: #383838;
                margin: 0;
                font-size: 16px;
                padding: 9px 23px;
                width: 100%;
                color: #cecece;
                text-transform: none;
                letter-spacing: 0;
            }

            input.wpcf7-form-control.wpcf7-submit {
                position: absolute;
                top: 0;
                right: 5px;
                border-radius: 0;
                color: #b4b4b4;
                background-color: transparent;
                border-color: transparent;
                width: 40px;
                height: 50px;
                line-height: 40px;
                font-size: 20px;
                padding: 0;
                font-family: "linea-arrows-10";
                cursor: pointer;

                &:hover {
                    color: $first-main-color;
                    background-color: transparent;
                    border-color: transparent;
                }

                &:focus {
                    outline: none;
                }
            }
        }

        &.eltdf-light {
            background-color: #ffffff;
        }

        .eltdf-footer-top-alignment-center{
            text-align: center;
        }
	    
        .eltdf-footer-top-alignment-left{
            text-align: left;
        }
	    
        .eltdf-footer-top-alignment-right{
            text-align: right;
        }

        .eltdf-footer-top-inner{
            box-sizing: border-box;
	        
            &.eltdf-grid{
                padding: 94px 0 60px;
            }
	        
            &.eltdf-full-width{
                padding: 76px 50px;
            }
        }
    }

    .eltdf-footer-bottom-holder {
        @include eltdfRelativeHolderLayout();
        background-color: #212121;
        box-sizing: border-box;
        text-align: center;
        &.eltdf-light {
            background-color: #ffffff;
        }

        .eltdf-footer-bottom-inner {
            box-sizing: border-box;
            .widget {
                margin-bottom: 0;
            }
	        
            &.eltdf-grid {
                padding: 45px 0;
                .eltdf-grid-row {
                    > div {
                        &:first-child {
                            text-align: left;
                        }
                        &:last-child {
                            text-align: right;
                        }
                    }
                }

            }
	        
            &.eltdf-full-width {
                padding: 55px 50px 45px;
                .eltdf-grid-row {
                    > div {
                        &:first-child {
                            text-align: left;
                        }
                        &:last-child {
                            text-align: right;
                        }
                    }
                }
            }

            ul li {
                display: inline-block;
                padding: 0 15px;
                a {
                  font-weight: 500;
                }
            }
        }
    }
}
/* ==========================================================================
   Footer style - end
   ========================================================================== */