/* ==========================================================================
# Search opener - begin
========================================================================== */

.eltdf-search-opener {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    font-size: 15px;
    color: $default-text-color;
    margin: 0 23px 0 7px;

    &:hover {
        color: $default-heading-color;
    }

    .eltdf-search-opener-wrapper {
        span {
            height: 22px;
            width: 20px;
            img {
                @include eltdfTransition(opacity .2s ease);
                &.eltdf-search-image-light {
                    opacity: 0;
                    @include eltdfTransform(translateY(-100%));
                }
            }
        }

        > * {
            position: relative;
            display: inline-block;
            vertical-align: middle;
        }

        .eltdf-icon-font-awesome,
        .eltdf-icon-font-elegant,
        .eltdf-icon-ion-icon {
            line-height: inherit;
        }
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-search-opener,
.eltdf-light-header .eltdf-top-bar .eltdf-search-opener {
    color: $header-light-color !important; //Color can be set inline from widget opener

    &:hover {
        color: $header-light-hover-color !important;
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-search-opener,
.eltdf-dark-header .eltdf-top-bar .eltdf-search-opener {
    color: $header-dark-color !important; //Color can be set inline from widget opener

    &:hover {
        color: $header-dark-hover-color !important;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-search-opener .eltdf-search-image,
.eltdf-light-header .eltdf-top-bar .eltdf-search-opener .eltdf-search-image{
    opacity: 0;
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-search-opener .eltdf-search-image-light,
.eltdf-light-header .eltdf-top-bar .eltdf-search-opener .eltdf-search-image-light{
    opacity: 1;
}

/* ==========================================================================
# Search opener - end
========================================================================== */