@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.eltdf-comment-holder {
    @include eltdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltdf-comment-holder-inner {
        padding: 41px 0 20px;
    }
	
	.eltdf-comment {
		
		&.eltdf-pingback-comment {
			
			.eltdf-comment-text {
				padding: 0;
			}
		}
	}

    .eltdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;


        li {
            @include eltdfRelativeHolderLayout();
            margin: 24px 0 0;
            .eltdf-comment {
                padding-bottom: 28px;
                border-bottom: 1px solid #e7e7e7;
            }
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .eltdf-comments-title {
        margin-bottom: 28px;

        > * {
            margin: 0;
        }
    }

    .eltdf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 82px;
        height: 82px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .eltdf-comment-text {
        padding: 0 0 0 108px;
        min-height: 82px;

        .eltdf-comment-info {
            @include eltdfRelativeHolderLayout();
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            margin-left: 10px;
            font-size: 12px;
            letter-spacing: .3em;
            text-transform: uppercase;
            color: $default-heading-color;

            &:hover {
                color: $first-main-color;
            }
        }

        .eltdf-comment-edit {
            position: absolute;
            right: 0;
            z-index: 9999;
        }

        .eltdf-comment-name {
            float: left;
            margin: 0;
            text-transform: uppercase;
        }

        .eltdf-comment-date {
            margin: 0;
            font-size: 12px;
            line-height: 26px;
            text-transform: uppercase;
            letter-spacing: .3em;
            color: #8c8c8c;
            font-weight: 500;
        }

        .eltdf-text-holder p {
            margin: 14px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 14px;
            text-transform: uppercase;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.eltdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .eltdf-comment-form-inner {
        padding: 45px 0 78px;
        #respond input[type=text], 
        #respond textarea, 
        .post-password-form input[type=password] {
            background-color: #f9f9f9;
        }
        #respond input[type='text'] {
            width: 49%;
        }
        #respond #author {
            margin-right: 2%;
        }
        #respond input[type=text] {
            float: left;
        }
        @include phone-portrait {
            #respond input[type='text'] {
                width: 100%;
            }
            #respond #author {
                margin-right: 0;
            }
        }
    }
    
    .comment-reply-title {
        margin: 0 0 37px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .eltdf-comment-holder {
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .eltdf-comment-holder {
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .eltdf-comment-text {
	        
            .eltdf-comment-date,
            .eltdf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .eltdf-comment-holder {

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

%input-style {
    width: 100%;
    margin: 0 0 15px;
    padding: 16px 24px 15px;
    font-family: inherit;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: .3em;
    line-height: 20px;
    font-weight: inherit;
    color: $default-heading-color;
    background-color: transparent;
    border: 1px solid #f9f9f9;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    box-sizing: border-box;
    @include eltdfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }
    
    @include eltdfPlaceholder {
        color: #aaaaaa;
    }
}

#respond textarea,
#respond input[type='text'],
#respond input[type='email'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

#respond textarea {
    display: block;
    padding: 15px 24px;
}

p.form-submit {
    margin: 0;
}

#submit_comment,
.post-password-form input[type='submit'] {
    @include eltdfButtonDefaultStyle();
    @include eltdfButtonMediumParams();
    @include eltdfButtonSolidColor();

    &:hover {
        @include eltdfButtonSolidHoverColor();
    }
}

%cf7-input-style {
    width: 100%;
    margin: 0 0 21px;
    padding: 9px 18px;
    font-family: inherit;
    font-size: 12px;
    text-transform: uppercase;
    line-height: 30px;
    letter-spacing: .2em;
    font-weight: 400;
    color: $default-text-color;
    background-color: transparent;
    border: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    box-sizing: border-box;

    &:focus {
        color: $default-heading-color;
        background-color: #fff;
    }
	
	@include eltdfPlaceholder {
		color: #737373;
	}
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %cf7-input-style;

        footer & {
            margin: 0;
        }
    }
}

input.wpcf7-form-control.wpcf7-submit {
    @include eltdfButtonDefaultStyle();
    @include eltdfButtonMediumParams();
    @include eltdfButtonSolidColor();

    &:hover {
        @include eltdfButtonSolidHoverColor();
    }
}

.wpcf7-form p {
    margin: 0;
}

div.wpcf7 .ajax-loader {
    display: block;
}

.wpcf7-form textarea {
    vertical-align: middle;
}

.eltdf-two-columns-form-without-space {

    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */
/* ==========================================================================
   #Global Slick Slider - begin
   ========================================================================== */
.eltdf-slick-slider {
    .eltdf-slick-dots {
        text-align: center;
        margin: 25px 0 0 0;
        padding: 0;


        li {
            position: relative;
            display: inline-block;
            margin: 0 5px;
            padding: 0;
            cursor: pointer;
            @include eltdfTransition(transform .3s cubic-bezier(.7,-.2,.3,2.5),background-color .15s ease-out);

            &.slick-active button {
                background-color: #212121;
                @include eltdfTransform(scale(1.2,1.2));
            }

            button {
                font-size: 0;
                line-height: 0;
                display: block;
                width: 8px;
                height: 8px;
                padding: 0;
                cursor: pointer;
                color: transparent;
                border: 0;
                outline: none;
                background-color: #cac9c9;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                border-radius: 50%;
                @include eltdfTransition(transform .3s cubic-bezier(.7,-.2,.3,2.5),background-color .15s ease-out);

                &:hover {
                    background-color: #212121;
                    @include eltdfTransform(scale(1.2,1.2));
                }
            }

            &.slick-active .eltdf-slick-dot-inner {
                background-color: $first-main-color;
            }
        }
    }
    .slick-arrow {
        position: absolute;
        top: 50%;
        z-index: 3;
        @include eltdfTransform(translateY(-50%));
    }

    .eltdf-prev-icon,
    .eltdf-next-icon {
        display: inline-block;
        vertical-align: middle;
        color: #ffffff;
        line-height: 1;
        @include eltdfTransition(color .2s ease-out);

        .eltdf-icon-arrow {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            font-size: 35px;
            line-height: inherit;

            &:before {
                display: block;
                line-height: inherit;
            }
        }
    }
        .eltdf-prev-icon {
        left: 30px;
        }
        .eltdf-next-icon {
            right: 30px;
        }
}

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltdf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
    
    &.eltdf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include eltdfTransform(translateY($pos));
            }
        }
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			@include eltdfTransform(translateY(-50%));
			
			&:hover {
				
				.eltdf-prev-icon,
				.eltdf-next-icon {
					color: $first-main-color;
				}
			}
		}
		
		.owl-prev {
			left: 30px;
		}
		
		.owl-next {
			right: 30px;
		}
		
		.eltdf-prev-icon,
		.eltdf-next-icon {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			color: #ffffff;
			line-height: 1;
			@include eltdfTransition(color .2s ease-out);
			
			.eltdf-icon-arrow {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 35px;
				line-height: inherit;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 30px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 8px;
				height: 8px;
				margin: 0 4px;
                transform: scale(1);
				border-radius: 3em;
				background-color: #cacaca;
				@include eltdfTransition(transform 0.3s cubic-bezier(0.7, -0.2, 0.3, 2.5), background-color 0.15s ease-out);
				box-sizing: border-box;
			}
			
			&:hover,
			&.active {
				
				span {
                    transform: scale(1.35, 1.35);
					background-color: $default-heading-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}
}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.eltdf-404-page {

    &.eltdf-wrapper {
        padding: 0 !important;
    }
    
    .eltdf-content {
        height: 80vh;
    }

    .eltdf-content-inner {
        position: relative;
        display: table;
        height: 100%;
        width: 100%;
    }

    .eltdf-page-not-found {
        position: relative;
        display: table-cell;
        width: 100%;
        height: 100%;
        vertical-align: middle;
        padding: 0 20%;
        text-align: center;

        .eltdf-page-not-found-title {
            margin: 0;
        }
	
	    .eltdf-page-not-found-subtitle {
            margin: 35px 0 0;
        }
	    
	    .eltdf-page-not-found-text {
		    margin: 10px 0 0;
	    }

        .eltdf-btn {
            margin: 30px 0 0;
        }
    }

    @include ipad-portrait {

        .eltdf-page-not-found {
            padding: 40px 30px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltdf-back-to-top {
    position: fixed;
    width: 55px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include eltdfTransition(all 0.5s ease);
        @include eltdfTransform(scale(0));
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include eltdfTransition(opacity 0.3s ease, transform 0.5s ease);
        @include eltdfTransform(scale(1));
    }

    > span {
        position: relative;
        display: block;
        width: 1em;
        height: 1em;
        line-height: 1em;
        font-size: 50px;
        text-align: center;
        color: $default-text-color;
        background-color: transparent;
        border: 1px solid transparent;
        @include eltdfTransition(all .2s linear);
        backface-visibility: hidden;

        &:hover {
            background-color: transparent;
            border: 1px solid transparent;

            @include eltdfTransform(scale(1.15) translateZ(0));
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.eltdf-self-hosted-video-holder {
    .eltdf-video-wrap {
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}
/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */