/* ==========================================================================
   Blog List - Masonry style - begin
   ========================================================================== */

.eltdf-blog-holder {
	
    &.eltdf-blog-masonry {

        &.eltdf-blog-normal-space article {
            margin: 0 0 60px;
        }

        article {
            margin: 0 0 20px;
            padding: 0 0 30px;

            &.eltdf-post-has-media {
                .eltdf-post-heading {
                    margin-bottom: 30px;
                }
            }

            .eltdf-post-title {
                margin: 0;
                text-transform: uppercase;
            }

            .eltdf-post-excerpt-holder {
                @include eltdfRelativeHolderLayout();
                margin: 0 0 17px;
            }

            .eltdf-more-link-container {
                @include eltdfRelativeHolderLayout();
                margin: 20px 0 0;
            }

            .eltdf-post-info-top {
                margin-bottom: 5px;
                text-transform: uppercase;
                letter-spacing: .2em;
                font-size:12px;
                font-weight: 500;
	            
                > div {
	                position: relative;
	                display: inline-block;
                    padding-right: 25px;
                    margin-left: 5px;
	                
	                &:after {
		                content: "/";
		                position: absolute;
		                top: 0;
		                right: 5px;
	                }
	                
	                &:first-child {
		                margin-left: 0;
	                }
	                
	                &:last-child {
		                
		                &:after {
			                content: '';
		                }
	                }
	
	                span {
		                color: #444;
	                }
                }
            }

            .eltdf-post-info-bottom {
                text-transform: uppercase;
                font-size: 12px;
                letter-spacing: .2em;


                .eltdf-post-info-bottom-right {
	                
                    > div {
                        margin-left: 15px;
	                    
	                    &:first-child {
		                    margin-left: 0;
	                    }
                    }
                }

                .eltdf-post-info-author {
                    display: inline-block;
	                
                    a {
                        text-transform: capitalize;
                        color: #444;
	                    
                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }

                .eltdf-post-info-bottom-right {
                    text-align: right;
                }

                .eltdf-post-info-comments-holder,
                .eltdf-blog-like {
                    display: inline-block;

                    a {
                        display: inline-block;

                        span,
                        i {
                            vertical-align: middle;
                            color: #444;
                        }
                    }
                }

                .eltdf-blog-like {
	                
                    i:first-child,
                    span:first-child {
                        @include eltdfTransition(color 0.15s ease-out);
                        margin-right: 7px;
                        color: #c4c3c3;
                    }

                    &:hover {
	                    
                        i:first-child,
                        span:first-child {
                            color: $first-main-color;
                        }
                    }
                }

                .eltdf-post-info-comments-holder {
	                
                    span:first-child {
                        @include eltdfTransition(color 0.15s ease-out);
                        margin-right: 7px;
                        color: #c4c3c3;
                    }
	                
                    &:hover {
	                    
                        span:first-child {
                            color: $first-main-color;
                        }
                    }
                }
            }

            &.format-audio {
                &.eltdf-post-no-media.has-post-thumbnail {
                    .eltdf-post-heading {
                        margin-bottom: 30px;
                    }
                }
            }

            &.format-link {
	            
                .eltdf-post-text {
	                padding: 35px 25px 29px;
	                background-color: #f9f9f9;
                }
	            
                .eltdf-post-text-main {
                    @include eltdfTableLayout();
                    text-align: left;
                }
	            
                .eltdf-post-mark {
                    @include eltdfTableCellLayout();
                    width: 50px;
                    vertical-align: top;
                    text-align: right;

                    .eltdf-link-mark {
                        padding-top: 5px;
                        font-size: 26px;
                        color: $first-main-color;
                    }
                }

                .eltdf-post-excerpt-holder {
                    padding-right: 35px;
                    box-sizing: border-box;
                    margin-bottom: 31px;
                }
	            
                .eltdf-post-link-holder {
                    @include eltdfTableCellLayout();
                    vertical-align: top;
                    text-align: left;
                }
	            
                .eltdf-post-title {
                    margin-bottom: 0;
                }
            }
	        
            &.format-quote {
	            
                .eltdf-post-text {
	                padding: 34px;
	                background-color: #f9f9f9;
                }
	            
                .eltdf-post-text-main {
                    position: relative;
                    text-align: left;
                }
	            
                .eltdf-post-mark {
                    position: absolute;
                    top: -24px;
                    right: -31px;
                    width: 50px;
                    vertical-align: top;
                    text-align: left;

                    .eltdf-quote-mark {
                        padding-top: 5px;
                        font-size: 38px;
                        color: #7c7c7c;
                    }
                }
	            
                .eltdf-post-link-holder {
                    @include eltdfTableCellLayout();
                    vertical-align: top;
                    text-align: left;
                }
	            
                .eltdf-post-title {
                    margin-bottom: 39px;
                }

                .eltdf-quote-author {
                    font-size: 12px;
                    letter-spacing: .2em;
                    text-transform: uppercase;
                }

                .eltdf-post-author {
	                display: inline-block;
	                margin-bottom: 23px;
	                font-size: 13px;
	                line-height: 20px;
                }
            }
        }
    }
}
/* ==========================================================================
   Blog List - Masonry style - end
   ========================================================================== */