.eltdf-blog-list-holder {
    &.eltdf-bl-simple {

        .eltdf-post-title {
            margin: 0;
            color: #414141;
            text-transform: none;
            letter-spacing: 0;
            font-size: 16px;
            font-weight: 400;

            a {
                color: #414141;
                text-transform: none;
                letter-spacing: 0;
                font-size: 16px;
                font-weight: 400;
            }
        }

        .eltdf-post-info-date {
            font-size: 12px;
            letter-spacing: .3em;
            text-transform: uppercase;
            line-height: 23px;
            a {
                color: #929292;
            }
        }

        .eltdf-post-image {
            display: table-cell;
            vertical-align: top;
            width: 90px;
            padding-top: 9px;
        }

        .eltdf-bli-content {
            display: table-cell;
            vertical-align: top;
            width: auto;
            padding-left: 20px;
        }
    }
}