/* ==========================================================================
   Blog List - Standard style - begin
   ========================================================================== */

.eltdf-blog-holder {
	
    &.eltdf-blog-standard {
	    
        article {
            margin: 0 0 50px;
            padding: 0;

            &.eltdf-post-has-media {
                .eltdf-post-heading {
                    margin-bottom: 37px;
                }
            }

            .eltdf-post-title {
                margin: 0 0 15px;
            }

            .eltdf-post-excerpt-holder {
                @include eltdfRelativeHolderLayout();
                margin: 0 0 20px;
            }

            .eltdf-more-link-container {
                @include eltdfRelativeHolderLayout();
                margin: 20px 0 0;
            }

            .eltdf-post-info-top {
                margin-bottom: 5px;
                text-transform: uppercase;
                font-size: 12px;
                letter-spacing: .2em;
	
	            > div {
		            position: relative;
		            display: inline-block;
		            padding-right: 25px;
		            margin-left: 5px;
		
		            &:after {
			            content: "/";
			            position: absolute;
			            top: 0;
			            right: 5px;
		            }
		
		            &:first-child {
			            margin-left: 0;
		            }
		
		            &:last-child {
			
			            &:after {
				            content: '';
			            }
		            }
		
		            span {
			            color: #444;
		            }
	            }
            }

            .eltdf-social-share-holder.eltdf-list li {
              margin-right: 12px;
              a {
                font-size: 13px;
              }
            }

            .eltdf-post-info-bottom {
	            padding-bottom: 10px;
	            border-bottom: 1px solid #e7e7e7;

                .eltdf-post-info-bottom-left,
                .eltdf-post-info-bottom-right {
	                position: relative;
	                display: inline-block;
	                width: 50%;
	                float: left;
                    line-height: 27px;
                }

                .eltdf-post-info-bottom-right {
	                
                    > div {
                        margin-left: 17px;
	                    
	                    &:first-child {
		                    margin-left: 0;
	                    }
                    }
                }

                .eltdf-post-info-author {
                    display: inline-block;
	                
                    a {
                        color: #444;
	                    text-transform: capitalize;
	                    
                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }

                .eltdf-post-info-bottom-right {
                    text-align: right;
                }

                .eltdf-post-info-comments-holder,
                .eltdf-blog-like {
                    display: inline-block;

                    a {
                        display: inline-block;

                        span,
                        i {
                            vertical-align: middle;
                            color: #444;
                        }
                    }
                }

                .eltdf-blog-like {
	                
                    i:first-child,
                    span:first-child {
                        margin-right: 7px;
	                    color: #c4c3c3;
	                    @include eltdfTransition(color 0.15s ease-out);
                    }

                    &:hover {
	                    
                        i:first-child,
                        span:first-child {
                            color: $first-main-color;
                        }
                    }
                }

                .eltdf-post-info-comments-holder {
	                
                    span:first-child {
                        margin-right: 7px;
	                    color: #c4c3c3;
	                    @include eltdfTransition(color 0.15s ease-out);
                    }
	                
                    &:hover {
	                    
                        span:first-child {
                            color: $first-main-color;
                        }
                    }
                }
            }

            &.format-audio {
                &.eltdf-post-no-media.has-post-thumbnail {
                    .eltdf-post-heading {
                        margin-bottom: 50px;
                    }
                }
            }

            &.format-link {
	            
                .eltdf-post-text{
	                padding: 50px 53px;
	                background-color: #fbfbfb;
                }
	            
                .eltdf-post-text-main {
                    display: block;
                    text-align: center;
                }
	            
                .eltdf-post-mark {
                    display: block;
                    text-align: center;
                    margin-bottom: 15px;

                    .eltdf-link-mark {
                        padding-top: 5px;
                        font-size: 26px;
                        color: $first-main-color;
                    }
                }
	            
                .eltdf-post-link-holder {
                    display: block;
                    text-align: center;
                }
	            
                .eltdf-post-title {
                    margin-bottom: 0;
                }
            }

            &.format-quote {
	            
                .eltdf-post-text {
	                padding: 57px 28%;
	                background-color: #fbfbfb;
                }
	            
                .eltdf-post-text-main {
                    display: block;
                    text-align: center;
                }
	            
                .eltdf-post-mark {
                    display: block;
                    text-align: center;

                    .eltdf-quote-mark {
                        padding-top: 5px;
                        font-size: 36px;
                        color: $first-main-color;
                    }
                }
	            
                .eltdf-post-link-holder {
                    display: block;
                    text-align: center;
                }
	            
                .eltdf-post-title {
                    margin:3px 0 5px;
                }

                .eltdf-quote-author {
                  font-size: 12px;
                  letter-spacing: .3em;
                  font-weight: 500;
                  text-transform: uppercase;
                }
	            
                .eltdf-post-author {
	                display: inline-block;
	                margin-bottom: 23px;
	                font-size: 13px;
	                line-height: 20px;
                }

                .eltdf-separator {
                  margin-top: 22px;
                }
            }
        }
    }
}
/* ==========================================================================
   Blog List - Standard style - end
   ========================================================================== */