/* ==========================================================================
   Blog Single Standard style - begin
   ========================================================================== */

.eltdf-blog-holder.eltdf-blog-single {

    &.eltdf-blog-single-standard {
    
        article {
            margin: 0 0 70px;
        
            .eltdf-post-heading {
                margin-bottom: 28px;
            }
        
            .eltdf-post-title {
                margin: 0 0 14px;
                line-height: 1em;
            }
        
            .eltdf-post-text {
                padding: 0 0 18px;
                border-bottom: 1px solid $default-border-color;
            }
        
            .eltdf-post-info-top {
                margin-bottom: 7px;
                text-transform: uppercase;
                font-size: 12px;
                letter-spacing: .3em;
            
                > div {
                    position: relative;
                    display: inline-block;
                    padding-right: 8px;
                    margin-right: 3px;
                    color: $default-text-color;
                
                    a {
                        color: inherit;
                    
                        &:hover {
                            color: $default-heading-color;
                        }
                    }
                
                    &:after {
                        content: "/";
                        position: absolute;
                        top: 0;
                        right: -2px;
                    }
                
                    &:last-child {
                        padding-right: 0;
                        margin-right: 0;
                    
                        &:after {
                            content: '';
                        }
                    }
                }
            }
        
            .eltdf-post-info-bottom {
                @include eltdfRelativeHolderLayout();
                margin: 31px 0 0;
            
                .eltdf-post-info-bottom-left,
                .eltdf-post-info-bottom-right {
                    position: relative;
                    display: inline-block;
                    width: 50%;
                    float: left;
                }
            
                .eltdf-post-info-bottom-left {
                
                    > div {
                        position: relative;
                        display: inline-block;
                        padding-right: 8px;
                        margin-right: 3px;
                        color: $default-text-color;
                    
                        a {
                            color: inherit;
                            text-transform: uppercase;
                            letter-spacing: .2em;
                            font-size: 12px;
                        
                            &:hover {
                                color: $first-main-color;
                            }
                        }
                    
                        &:after {
                            content: "/";
                            position: absolute;
                            top: 0;
                            right: -2px;
                        }
                    
                        &:last-child {
                            padding-right: 0;
                            margin-right: 0;
                        
                            &:after {
                                content: '';
                            }
                        }
                    }
                }
            
                .eltdf-post-info-bottom-right {
                    text-align: right;
                
                    > div {
                        margin-left: 17px;
                    
                        &:first-child {
                            margin-left: 0;
                        }
                    }
                }
            }
        
            &.format-link {
            
                .eltdf-post-text {
                    padding: 50px 53px;
                    background-color: #fbfbfb;
                    border-bottom: none;
                    margin-bottom: 40px;
                }

                .eltdf-post-text-main {
                    display: block;
                    text-align: center;
                }

                .eltdf-post-mark {
                    display: block;
                    text-align: center;
                    margin-bottom: 15px;

                    .eltdf-link-mark {
                        padding-top: 5px;
                        font-size: 26px;
                        color: $first-main-color;
                    }
                }

                .eltdf-post-link-holder {
                    display: block;
                    text-align: center;
                }
            
                .eltdf-post-title {
                    margin-bottom: 0;
                }

                .eltdf-post-info-bottom {
                    padding: 0 0 18px;
                    border-bottom: 1px solid #d7d7d7;
                }
            }
        
            &.format-quote {
            
                .eltdf-post-text {
                    padding: 57px 28%;
                    background-color: #fbfbfb;
                    border-bottom: none;
                    margin-bottom: 40px;
                }

                .eltdf-post-text-main {
                    display: block;
                    text-align: center;
                }

                .eltdf-post-mark {
                    display: block;
                    text-align: center;

                    .eltdf-quote-mark {
                        padding-top: 5px;
                        font-size: 36px;
                        color: $first-main-color;
                    }
                }

                .eltdf-post-link-holder {
                    display: block;
                    text-align: center;
                }

                .eltdf-post-title {
                    margin:3px 0 5px;
                }

                .eltdf-quote-author {
                    font-size: 12px;
                    letter-spacing: .3em;
                    font-weight: 500;
                    text-transform: uppercase;
                }

                .eltdf-post-author {
                    display: inline-block;
                    margin-bottom: 23px;
                    font-size: 13px;
                    line-height: 20px;
                }

                .eltdf-separator {
                    margin-top: 22px;
                }

                .eltdf-post-info-bottom {
                    padding: 0 0 18px;
                    border-bottom: 1px solid #d7d7d7;
                }
            }
        }
    }
}
/* ==========================================================================
   Blog Single Standard style - end
   ========================================================================== */